*{margin:0;padding:0;box-sizing:border-box}body{overflow-x:hidden !important}h1{font-family:"Rajdhani",sans-serif;font-size:4em;line-height:1em}h2{font-family:"Rajdhani",sans-serif;font-size:3.5em;line-height:1em}h3{font-family:"Rajdhani",sans-serif;font-size:3em;line-height:1em}h4{font-family:"Rajdhani",sans-serif;font-size:2.5em;line-height:1em}h5{font-family:"Rajdhani",sans-serif;font-size:2em;line-height:1em}h6{font-family:"Rajdhani",sans-serif;font-size:1.5em;line-height:1em}li,p{font-family:"Mali",cursive;font-size:1em;line-height:1em}a{color:white;font-style:none;text-decoration:none;font-size:1em;line-height:1em}.section{position:-webkit-sticky;position:sticky;height:100vh;color:#fff}.container{padding-top:5vh;margin-left:10vw;margin-right:10vw;margin-bottom:5vh}@media (min-width: 768px){.container{padding-top:10vh;margin-left:10vw;margin-right:10vw;margin-bottom:10vh}}@media (min-width: 1250px){.container{padding-top:10vh;margin-left:10vw;margin-right:10vw;margin-bottom:10vh}}.container__alt{padding-top:15vh;margin-left:10vw;margin-right:10vw;margin-bottom:5vh}@media (min-width: 768px){.container__alt{padding-top:20vh;margin-left:10vw;margin-right:10vw;margin-bottom:20vh}}@media (min-width: 1250px){.container__alt{padding-top:10vh;margin-left:10vw;margin-right:10vw;margin-bottom:10vh}}.loader-page{background:#833ab4;background:linear-gradient(90deg, #833ab4 0%, #ff0025 50%, #ffd23f 100%);height:100vh;width:100vw;margin:auto;display:flex;justify-content:center}.loader-logo{position:absolute;top:20%}.first{background-color:#00a6ed;color:black}.phrase{font-size:1.3em}.btn_social{font-size:1.5em;line-height:1em;padding-top:10px;cursor:pointer;color:black}.right{margin-left:5px}.btn_social:hover{color:white}.btn_social:active{-webkit-transform:scale(0.7);transform:scale(0.7)}.second{background-color:#533a71}.title-second{font-size:2.5em;margin-bottom:10px}.text-second{font-size:1.2em;margin-bottom:15px}.contact-title{font-size:2.5em;margin-bottom:5px}.container_