k;text-decoration:none}.carousel .carousel__item,.carousel .carousel__item--hidden{display:none}.img-project{width:100%}.carousel .carousel__item--visible{display:block;-webkit-animation:fadeVisibility 0.8s;animation:fadeVisibility 0.8s}.title-project{font-size:2.5em;padding-bottom:15px}.text-card{padding-bottom:15px;font-size:1.3em}.carousel__actions{display:grid;width:100%;grid-template-rows:1fr;grid-template-columns:1fr 1fr;padding:0 auto}.btn{display:inline-block;padding:0.5em 3em;border-radius:2em;box-sizing:border-box;text-decoration:none;font-family:"Rajdhani",sans-serif;font-size:1em;font-weight:bolder;color:#ffb400;background-color:black;text-align:center;transition:all 0.5s;border-style:none;cursor:pointer}@media all and (max-width: 30em){.btn{margin:0.2em auto}}.btn:active{-webkit-transform:scale(0.5);transform:scale(0.5)}@-webkit-keyframes fadeVisibility{0%{opacity:0}100%{opacity:1;-webkit-transform:opacity linear;transform:opacity linear}}@keyframes fadeVisibility{0%{opacity:0}100%{opacity:1;-webkit-transform:opacity linear;transform:opacity linear}}

